#idCourses {
  padding-bottom: 5%;
}

.containerGridCourses {
  display: grid;
  overflow: hidden;
  height: auto;
  justify-content: center;
  gap: 2em;
}

.gridSliceCourses {
  grid-template-columns: repeat(4, 22%);
}

.containerProjectCourses {
  color: #330033;
}

.containerProjectCourses > img {
  transition: linear box-shadow 0.2s;
  box-shadow: 5px 5px 10px var(--bgBaseSecondary);
}

.containerProjectCourses > img:hover {
  box-shadow: 5px 5px 10px var(--bgBasePrimary);
}

.containerProjectCourses > img {
  width: 100%;
  height: 200px;
}

.containerProjectCourses > p {
  text-align: center;
}

.titlePortfolioCourses {
  text-transform: uppercase;
  font-family: "Quicksand", sans-serif;
  text-align: center;
  color: black;
  padding: 20px;
}

.divButtonFlx {
  display: flex;
  justify-content: center;
  margin: 0% 0% 3% 0%;
}

.divButtonFlx > button {
  color: white;
  background-color: var(--bgBaseSecondary);
  border: 1px solid var(--bgBaseSecondary);
  padding: 12px;
  text-transform: uppercase;
  border-radius: 20px;
  transition: linear background-color 0.2s, linear border 0.2s;
}

.divButtonFlx > button:hover {
  color: white;
  background-color: var(--bgBaseTerciary);
  border: 1px solid var(--bgBaseTerciary);
}

@media screen and (max-width: 600px) {
  .divButtonFlx {
    margin: 3% 0%;
  }

  .containerProjectCourses > img {
    height: 150px;
  }

  .gridSliceCourses {
    grid-template-columns: repeat(1, 270px);
  }
}
