.containerGrid {
  display: grid;
  overflow: hidden;
  height: auto;
  justify-content: center;
  padding-bottom: 4%;
  gap: 2em;
}

.gridSlicePortfolio {
  grid-template-columns: repeat(3, 30.2%);
}

.containerProjects {
  position: relative;
  cursor: pointer;
  color: #330033;
}

.containerProjects > img {
  width: 100%;
  height: 200px;
  transition: linear box-shadow 0.2s;
  box-shadow: 5px 5px 10px var(--bgBaseSecondary);
}

.containerProjects > img:hover {
  box-shadow: 5px 5px 10px var(--bgBasePrimary);
}

.overlay {
  position: absolute;
  bottom: 0;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5); /* Black see-through */
  color: #f1f1f1;
  width: 100%;
  transition: 0.4s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
  margin: 0%;
}

.containerProjects:hover .overlay {
  opacity: 1;
}

.titlePortfolio {
  text-transform: uppercase;
  font-family: "Quicksand", sans-serif;
  text-align: center;
  color: black;
  padding: 20px;
}

.divButtonFlx {
  display: flex;
  justify-content: center;
  margin: 2% 0%;
}

.divButtonFlx > button {
  padding: 12px;
  color: white;
  background-color: var(--bgBaseGreenDark);
  text-transform: uppercase;
  border: none;
  border-radius: 20px;
}

@media screen and (max-width: 600px) {
  .containerProjects > img {
    height: 150px;
  }

  .gridSlicePortfolio {
    grid-template-columns: repeat(1, 300px);
  }
}
